import axios from "axios";
import { useEffect, useRef, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import { useCart } from "./CartContext";
import { timeAgo } from "../utils/timeAgo";
import "./review.css";

const ProductDetails = () => {
  const { id } = useParams(); // this gets the ID from the URL
  const [service, setService] = useState(null);
  const [centreId, setCentreId] = useState(null);
  const [showDoctorModal, setShowDoctorModal] = useState(false);
  const [selectedDoctor, setSelectedDoctor] = useState(null);
  const [reviewData, setReviewData] = useState({ rating: 0, review: "" });
  const [submitting, setSubmitting] = useState(false);
  const [reviews, setReviews] = useState([]);
  const [loading, setLoading] = useState(true);
  const { activeTab } = useCart();

  useEffect(() => {
    const fetchService = async () => {
      try {
        const response = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/centreService/single/${id}`
        );
        setService(response.data);
        setCentreId(response.data?.service?.centre?._id);
      } catch (error) {
        console.error("Error fetching service:", error);
      }
    };

    fetchService();
  }, [id]);

  const { addToCart } = useCart();

  const navigate = useNavigate();

  const handleBookNow = async (serviceId, fromPage) => {
    try {
      const response = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/centreService/single/${serviceId}`
      );
      const selectedService = response.data;
      addToCart(selectedService); // from context
      navigate("/cart", { state: { from: fromPage } });
    } catch (error) {
      console.error("Error adding to cart", error);
    }
  };

  const handleDoctorClick = (doctor) => {
    setSelectedDoctor(doctor);
    setShowDoctorModal(true);
  };

  const closeDoctorModal = () => {
    setShowDoctorModal(false);
    setSelectedDoctor(null);
  };
  useEffect(() => {
    const savedDraft = JSON.parse(localStorage.getItem("pendingReview"));
    if (savedDraft && savedDraft.serviceId === id) {
      setReviewData(savedDraft.data);
    } else {
      // Clear old draft if it belongs to another service
      localStorage.removeItem("pendingReview");
    }
  }, [id]);

  const handleRatingClick = (rating) => {
    setReviewData((prev) => ({ ...prev, rating }));
  };

  const handleReviewSubmit = async (e) => {
    // e.preventDefault();
    // if (!reviewData.rating && !reviewData.review.trim()) {
    //   alert("Please provide at least a rating or a review");
    //   return;
    // }
    e.preventDefault();

    if (
      (!reviewData.rating || reviewData.rating <= 0) &&
      !reviewData.review.trim()
    ) {
      alert("Please provide rating or review");
      return;
    }

    const userId = localStorage.getItem("userId");
    if (!userId) {
      localStorage.setItem(
        "pendingReview",
        JSON.stringify({
          serviceId: id,
          data: reviewData,
        })
      );
      navigate("/review-login");
      return;
    }

    setSubmitting(true);
    try {
      // ‚úÖ FIXED: Prepare data properly - only send what user provided
      const payload = {
        userId,
        serviceId: id,        ...(reviewData.rating > 0 && { rating: reviewData.rating }),
        ...(reviewData.review.trim() && { review: reviewData.review.trim() }),
      };
      await axios.post(
        `${process.env.REACT_APP_BACKEND_URL}/api/review`,
        payload
      );
      alert("Review submitted successfully!");
      setReviewData({ rating: 0, review: "",type:"service"});
      localStorage.removeItem("pendingReview");
    } catch (error) {
      console.error("Error submitting review:", error);
      // alert("Failed to submit review. Please try again.");
      alert(
        error?.response?.data?.message ||
          "Failed to submit review. Please try again."
      );
      // setReviewData({ rating: 0, review: "" });
    } finally {
      setSubmitting(false);
    }
  };

  //   setSubmitting(true);

  //   try {
  //     // Build payload dynamically
  //     const payload = {
  //       userId,
  //       serviceId: id,
  //       review: reviewData.review.trim(),
  //     };

  //     // Only include rating if user clicked a star
  //     if (reviewData.rating && reviewData.rating > 0) {
  //       payload.rating = reviewData.rating;
  //     }

  //     await axios.post(`${process.env.REACT_APP_BACKEND_URL}/api/review`, payload);

  //     alert("Review submitted successfully!");
  //     setReviewData({ rating: 0, review: "" });
  //     localStorage.removeItem("pendingReview");
  //   } catch (error) {
  //     console.error("Error submitting review:", error);
  //     alert(error.response?.data?.message || "Failed to submit review.");
  //   } finally {
  //     setSubmitting(false);
  //   }
  // };

  const modalRef = useRef(null);
  const viewModalRef = useRef(null);

  useEffect(() => {
    // Handle click outside
    const handleClickOutside = (event) => {
      if (
        modalRef.current &&
        !modalRef.current.contains(event.target) &&
        showDoctorModal
      ) {
        showDoctorModal(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    // üîë Scroll lock logic
    if (showDoctorModal) {
      document.body.style.overflow = "hidden"; // Disable background scroll
    } else {
      document.body.style.overflow = "auto"; // Enable background scroll
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
      document.body.style.overflow = "auto"; // Cleanup when component unmounts
    };
  }, [showDoctorModal]);

  useEffect(() => {
    const fetchReviews = async () => {
      try {
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/review/service/${id}`
        );
        setReviews(res.data.reviews || []);
      } catch (err) {
        console.error("Error fetching reviews:", err);
      } finally {
        setLoading(false);
      }
    };

    fetchReviews();
  }, [id]);

  if (loading) return <p>Loading reviews...</p>;

  return (
    <div className="product-details mt-32 padding-b-120">
      <div className="container container-two">
        <div className="row gy-4">
          <div className="col-lg-8">
            <div className="tab-content" id="pills-tabContent">
              <div
                // className="tab-pane fade show active"
                className={`tab-pane fade ${
                  activeTab === "details" ? "show active" : ""
                }`}
                id="pills-product-details"
                role="tabpanel"
                aria-labelledby="pills-product-details-tab"
                tabIndex={0}
              >
                {/* Product Details Content Start */}
                <div className="product-details">
                  <div className="product-details__thumb"></div>

                  <p className="product-details__desc">
                    {service?.service?.description}
                  </p>

                  <div>
                    <ul>
                      <li className=""  style={{ display: "flex", gap: "10px", alignItems: "center" }}>
                        <span className="name">Category:</span>
                        <span className="details" style={{ marginLeft: "0px" }}>
                          {service?.service?.serviceCategory}
                        </span>
                      </li>
                    </ul>
                  </div>


                  <div className="product-details__item mt-4">
                    <h6 className="product-details__title mb-3 ">Facilities</h6>

                    {service?.service?.centre?.availableFacilities?.length >
                    0 ? (
                      <ul
                        className="product-list"
                        style={{
                          display: "grid",
                          gridTemplateColumns: "1fr 1fr", // 2 columns
                          gap: "0px 20px",
                          paddingLeft: "17px",
                          margin: 0,
                        }}
                      >
                        {service.service.centre.availableFacilities.map(
                          (facility, index) => (
                            <li
                              className="product-list__item"
                              key={index}
                              style={{ margin: "3px 0" }} // reduce vertical gap between items
                            >
                              {facility}
                            </li>
                          )
                        )}
                      </ul>
                    ) : (
                      <p>Facilities Not Mentioned</p>
                    )}
                  </div>

                  <div className="product-details__item">
                    <h6 className="product-details__title mb-3">
                      Insurance Availability
                    </h6>
                    {service?.service?.centre?.availableInsurance.length > 1 ? (
                      <ul
                        className="product-list"
                        style={{
                          display: "grid",
                          gridTemplateColumns: "1fr 1fr", // 2 columns
                          gap: "0px 20px",
                          paddingLeft: "17px",
                          margin: 0,
                        }}
                      >
                        {service?.service?.centre?.availableInsurance?.map(
                          (insurance, index) => (
                            <li
                              className="product-list__item"
                              key={index}
                              style={{
                                margin: "3px 0", // <-- reduce vertical gap between items
                              }}
                            >
                              {insurance}
                            </li>
                          )
                        )}
                      </ul>
                    ) : (
                      <p>No Insurance Available</p>
                    )}
                  </div>
                </div>

                {/* Review Form Start */}
                {/* <div className="review-form mt-5  d-none d-lg-block">
                  <h5>Write a Review</h5>
                  <form onSubmit={handleReviewSubmit}>
                    <div className="mb-3">
                      <label className="fw-bold">Rating</label>
                      <div className="stars">
                        {[1, 2, 3, 4, 5].map((star) => {
                          const fillValue = reviewData.rating;
                          return (
                            <span
                              key={star}
                              className={`star ${
                                star <= fillValue ? "filled" : ""
                              }`}
                              onClick={() => handleRatingClick(star)}
                            >
                              ‚òÖ
                            </span>
                          );
                        })}
                      </div>
                      <small className="text-muted">
                        Click to rate (1-5 stars)
                      </small>
                    </div>

                    <div className="mb-3">
                      <label className="fw-bold">Your Review</label>
                      <textarea
                        placeholder="Share your experience with this service..."
                        value={reviewData.review}
                        onChange={(e) =>
                          setReviewData((prev) => ({
                            ...prev,
                            review: e.target.value,
                          }))
                        }
                        rows="4"
                        maxLength="1000"
                        // required
                      />
                      <small>{reviewData.review.length}/1000 characters</small>
                    </div>

                    <button
                      type="submit"
                      className="submit-btn"
                      disabled={
                        submitting ||
                        (!reviewData.rating && !reviewData.review.trim())
                      }
                      // disabled={
                      //   submitting ||
                      //   !reviewData.rating ||
                      //   !reviewData.review.trim()
                      // }
                    >
                      {submitting ? "Submitting..." : "Submit Review"}
                    </button>
                  </form>
                </div> */}

                <div className="review-section d-none d-lg-block">
  <div className="review-card">
    <h5 className="review-title">Write a Review</h5>

    <form onSubmit={handleReviewSubmit}>
      {/* Rating */}
      <div className="review-group">
        <label>
          Rating <span>(Optional)</span>
        </label>

        <div className="review-stars">
          {[1, 2, 3, 4, 5].map((star) => (
            <span
              key={star}
              className={`review-star ${
                star <= reviewData.rating ? "filled" : ""
              }`}
              onClick={() => handleRatingClick(star)}
            >
              ‚òÖ
            </span>
          ))}
        </div>

        <small>Click to rate (1‚Äì5 stars) ‚Äì Optional</small>
      </div>

      {/* Review Text */}
      <div className="review-group">
        <label>Your Review</label>

        <textarea
          placeholder="Share your experience with this service..."
          value={reviewData.review}
          onChange={(e) =>
            setReviewData((prev) => ({
              ...prev,
              review: e.target.value,
            }))
          }
          rows="4"
          maxLength="1000"
        />

        <small>{reviewData.review.length}/1000 characters</small>
      </div>

      <button
        type="submit"
        className="review-submit"
        disabled={
          submitting ||
          (!reviewData.rating && !reviewData.review.trim())
        }
      >
        {submitting ? "Submitting..." : "Submit Review"}
      </button>
    </form>
  </div>
</div>

                {/* Review Form End */}

                {/* Product Details Content End */}
              </div>

              <div
                // className="tab-pane fade "
                className={`tab-pane fade ${
                  activeTab === "ratings" ? "show active" : ""
                }`}
                id="pills-rating"
                role="tabpanel"
                aria-labelledby="pills-rating-tab"
                tabIndex={0}
              >
                <div className="product-review-wrapper">
                  {loading ? (
                    <p>Loading reviews...</p>
                  ) : reviews.length > 0 ? (
                    reviews.map((review) => (
                      <div className="product-review" key={review._id}>
                        <div className="product-review__top flx-between">
                          {/* ‚≠ê Rating */}
                          <div className="product-review__rating flx-align">
                            <div className="d-flex align-items-center gap-1">
                              <ul className="star-rating">
                                {[1, 2, 3, 4, 5].map((star) => (
                                  <li
                                    key={star}
                                    className="star-rating__item font-11"
                                    style={{
                                      color:
                                        star <= review.rating
                                          ? "#ffc107"
                                          : "#ddd",
                                    }}
                                  >
                                    <i className="fas fa-star" />
                                  </li>
                                ))}
                              </ul>
                              <span className="star-rating__text text-body">
                                {typeof review.rating === "number"
                                  ? review.rating.toFixed(1)
                                  : "0.0"}
                                {/* {typeof review.rating === "number" ? review.rating.toFixed(1) : "0.0"} */}
                              </span>
                            </div>
                          </div>

                          {/* üë§ Reviewer name + time */}
                          <div className="product-review__date">
                            by{" "}
                            <span className="product-review__user text-primary">
                              {review.userName}
                            </span>{" "}
                            {timeAgo(review.updatedAt)}
                          </div>
                        </div>

                        {/* üìù Review text */}
                        <div className="product-review__body">
                          <p className="product-review__desc">
                            {review.review}
                          </p>
                        </div>
                      </div>
                    ))
                  ) : (
                    <p className="text-muted">
                      No reviews found for this service.
                    </p>
                  )}
                </div>
              </div>

              <div
                className="tab-pane fade"
                id="pills-comments"
                role="tabpanel"
                aria-labelledby="pills-comments-tab"
                tabIndex={0}
              >
              </div>
            </div>
          </div>

          {/* ======================= Product Sidebar Start ========================= */}
          {activeTab !== "ratings" && (
            <div className="col-lg-4">
              <div className="product-sidebar section-bg">
                <div className="product-sidebar__top position-relative flx-between gap-1">
                  <button type="button" className="font-heading font-18">
                    Book Appointment
                  </button>

                  <div className="flx-align gap-2">
                    <h6 className="product-item__price text-success mb-0">
                      ‚Çπ{service?.service?.totalPrice}
                    </h6>
                    {service?.service?.discountPrice ? (
                      <span className="product-item__prevPrice text-decoration-line-through">
                        ‚Çπ{service?.service?.actualPrice}
                      </span>
                    ) : null}
                  </div>
                </div>
                <Link
                  to="/cart"
                  state={{ from: `/service-details/${service?.service?._id}` }}
                  className="btn btn-main d-flex w-100 justify-content-center align-items-center gap-2 pill px-sm-5 mt-32"
                  onClick={() =>
                    handleBookNow(
                      service?.service?._id,
                      `/service-details/${service?.service?._id}`
                    )
                  }
                >
                  <img src="/assets/images/icons/add-to-cart.svg" alt="" />
                  Book Now
                </Link>
                {/* Author Details Start*/}
                <div className="author-details">
                  {service?.service?.doctors?.map((doctor, index) => (
                    <div
                      key={doctor._id || index}
                      className="d-flex align-items-center gap-2 mb-3"
                    >
                      <div className="author-details__thumb flex-shrink-0">
                        <img
                          src={doctor.profilePhoto}
                          alt={doctor.name}
                          style={{
                            borderRadius: "50%",
                            width: "73x",
                            height: "70px",
                            objectFit: "cover",
                          }}
                          onClick={() => handleDoctorClick(doctor)}
                        />
                      </div>

                      <div className="author-details__content">
                        <h6 className="author-details__name font-18 mb-1">
                          <button
                            className=" p-0 link text-decoration-none border-0 bg-transparent"
                            style={{
                              fontSize: "inherit",
                              fontWeight: "inherit",
                              color: "black",
                            }}
                            onClick={() => handleDoctorClick(doctor)}
                          >
                            {doctor.name}
                          </button>
                        </h6>
                        <div
                          style={{
                            display: "-webkit-box",
                            WebkitBoxOrient: "vertical",
                            WebkitLineClamp: 4,
                            overflow: "hidden",
                            maxHeight: "5.9rem",
                            textOverflow: "ellipsis",
                            whiteSpace: "normal", // ‚úÖ allow wrapping
                          }}
                        >
                          <span className="text">{doctor.specialization}</span>
                          <span className="text">{doctor.qualification} </span>
                        </div>
                        <button
                          className="text-primary text-decoration-underline"
                          onClick={() => handleDoctorClick(doctor)}
                        >
                          View More
                        </button>
                      </div>
                    </div>
                  ))}
                </div>
                {/* Author Details End */}
                {/* Meta Attribute List Start */}
                <ul className="meta-attribute">
                  <li className="meta-attribute__item">
                    <span className="name">Availability Time</span>
                    <span className="details d-flex flex-wrap gap-2">
                      {service?.service?.slots?.map((slot) => (
                        <p
                          // to="#"
                          key={slot._id}
                          // className="text-primary"
                        >
                          {slot.day}: {slot.from} - {slot.to}
                        </p>
                      ))}
                    </span>
                  </li>
                </ul>
                {/* Meta Attribute List End */}
                {/* <div className="author-details__content"> */}
                <span className="name" style={{ color: "black" }}>
                  Doctor Report Uploading Time:
                </span>{" "}
                <span className="name"> {service?.service?.reportTiming}</span>
                {/* </div> */}
                <div
                  style={{
                    marginTop: "20px",
                    width: "100%",
                    maxWidth: "800px",
                    height: "300px",
                    borderRadius: "8px",
                    overflow: "hidden",
                    boxShadow: "0 4px 12px rgba(0,0,0,0.15)",
                  }}
                  dangerouslySetInnerHTML={{
                    __html: service?.service?.centre?.googleMapLink,
                  }}
                ></div>
                {/* Review Form for small screens */}
                <div className="review-section d-block d-lg-none">
                  <div className="review-card">
                    <h5 className="review-title">Write a Review</h5>

                    <form onSubmit={handleReviewSubmit}>
                      {/* Rating */}
                      <div className="review-group">
                        <label>
                          Rating <span>(Optional)</span>
                        </label>

                        <div className="review-stars">
                          {[1, 2, 3, 4, 5].map((star) => (
                            <span
                              key={star}
                              className={`review-star ${
                                star <= reviewData.rating ? "filled" : ""
                              }`}
                              onClick={() => handleRatingClick(star)}
                            >
                              ‚òÖ
                            </span>
                          ))}
                        </div>

                        <small>Click to rate (1‚Äì5 stars) ‚Äì Optional</small>
                      </div>

                      {/* Review Text */}
                      <div className="review-group">
                        <label>Your Review</label>

                        <textarea
                          placeholder="Share your experience with this service..."
                          value={reviewData.review}
                          onChange={(e) =>
                            setReviewData((prev) => ({
                              ...prev,
                              review: e.target.value,
                            }))
                          }
                          rows="4"
                          maxLength="1000"
                        />

                        <small>
                          {reviewData.review.length}/1000 characters
                        </small>
                      </div>

                      <button
                        type="submit"
                        className="review-submit"
                        disabled={
                          submitting ||
                          (!reviewData.rating && !reviewData.review.trim())
                        }
                      >
                        {submitting ? "Submitting..." : "Submit Review"}
                      </button>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Doctor Details Modal */}
      {showDoctorModal && selectedDoctor && (
        <div
          style={{
            position: "fixed",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: "rgba(0,0,0,0.5)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 1000,
          }}
        >
          <div
            style={{
              backgroundColor: "#fff",
              borderRadius: "10px",
              maxWidth: "800px",
              width: "95%",
              maxHeight: "95vh",
              boxShadow: "0 0 10px rgba(0,0,0,0.3)",
              position: "relative",
              overflowY: "auto",
              paddingBottom: "20px",
            }}
          >
            {/* Header */}
            <div
              style={{
                backgroundColor: "#3a3a3a",
                color: "#fff",
                padding: "15px 20px",
                fontSize: "18px",
                fontWeight: "bold",
                position: "sticky",
                top: 0,
                zIndex: 1,
              }}
            >
              <i className="fas fa-user-md me-2"></i> Doctor Details
              <button
                onClick={closeDoctorModal}
                style={{
                  position: "absolute",
                  top: "10px",
                  right: "10px",
                  background: "transparent",
                  border: "none",
                  fontSize: "22px",
                  color: "#fff",
                  cursor: "pointer",
                }}
              >
                &times;
              </button>
            </div>

            {/* Body */}
            <div
              style={{
                padding: "20px",
                display: "flex",
                flexWrap: "wrap",
                gap: "20px",
                color: "#333",
              }}
            >
              {/* Left Column: Photo */}
              <div style={{ flex: 1, minWidth: "250px", textAlign: "center" }}>
                <img
                  src={selectedDoctor.profilePhoto}
                  alt={selectedDoctor.name}
                  style={{
                    width: "150px",
                    height: "150px",
                    objectFit: "cover",
                    borderRadius: "50%",
                    border: "4px solid #e3f2fd",
                    boxShadow: "0 0 5px rgba(0,0,0,0.3)",
                  }}
                />
              </div>

              {/* Right Column: Info with Icons */}
              <div style={{ flex: 2, minWidth: "250px" }}>
                <h4 className="mb-3 fw-bold">{selectedDoctor.name}</h4>

                {/* Specialization */}
                {selectedDoctor.specialization && (
                  <div style={{ marginBottom: "10px", alignItems: "center" }}>
                    <i className="fas fa-stethoscope  me-2"></i>
                    <strong>Specialization:</strong>&nbsp;
                    <br />
                    <span>{selectedDoctor.specialization}</span>
                  </div>
                )}

                {/* Qualification */}
                {selectedDoctor.qualification && (
                  <div style={{ marginBottom: "10px", alignItems: "center" }}>
                    <i className="fas fa-graduation-cap  me-2"></i>
                    <strong>Qualification:</strong>&nbsp;
                    <span>{selectedDoctor.qualification}</span>
                  </div>
                )}

                {/* Experience */}
                {selectedDoctor.experience && (
                  <div
                    style={{
                      marginBottom: "10px",
                      display: "flex",
                      alignItems: "center",
                    }}
                  >
                    <i className="fas fa-calendar-alt  me-2"></i>
                    <strong>Experience:</strong>&nbsp;
                    <br />
                    <span>{selectedDoctor.experience}</span>
                  </div>
                )}

                {/* About */}
                {selectedDoctor.aboutDoctor && (
                  <div
                    style={{ marginBottom: "10px", alignItems: "flex-start" }}
                  >
                    <i className="fas fa-info-circle me-2 mt-1"></i>

                    <strong>About:</strong>
                    <p style={{ marginTop: "5px" }}>
                      {selectedDoctor.aboutDoctor}
                    </p>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default ProductDetails;